<template>
  <v-hover>
    <v-col cols="auto">
      <v-dialog transition="dialog-bottom-transition" max-width="600">
        <template v-slot:activator="{ on, attrs }">
          <v-btn color="primary" v-bind="attrs" v-on="on"
            >From the bottom</v-btn
          >
        </template>
        <template v-slot:default="dialog">
          <v-card>
            <v-toolbar color="primary" dark>Opening from the bottom</v-toolbar>
            <v-card-text>
              <div class="text-h2 pa-12">Hello world!</div>
            </v-card-text>
            <v-card-actions class="justify-end">
              <v-btn text @click="dialog.value = false">Close</v-btn>
            </v-card-actions>
          </v-card>
        </template>
      </v-dialog>
    </v-col>
    <template v-slot:default="{ hover }">
      <v-card class="mx-auto" max-width="344" elevation="0">
        <v-card flat color="transparent" max-width="350px">
          <v-card-title>
            <v-layout>
              <v-flex align-self-center>
                <v-card
                  :href="app.platforms[0].link"
                  target="_blank"
                  elevation="5"
                  style="border-radius: 20px"
                  width="100px"
                  height="100px"
                >
                  <v-img
                    :src="logo(app.logo)"
                    max-height="100"
                    max-width="100"
                  />
                </v-card>
              </v-flex>
              <v-flex xs7>
                <div>{{ app.name }}</div>
                <div>
                  <v-chip small pill color="blue" dark>{{ app.price }}</v-chip>
                </div>

                <template v-for="(platform, i) in app.platforms">
                  <!-- <div> -->
                  <v-btn :href="platform.link" target="_blank" icon :key="i">
                    <v-icon>{{ platform.icon }}</v-icon>
                  </v-btn>
                  <!-- </div> -->
                </template>
              </v-flex>
            </v-layout>
          </v-card-title>
          <v-fade-transition>
            <v-overlay v-if="hover" absolute color="#121212">
              <v-btn v-bind="attrs" v-on="on">See more info</v-btn>
            </v-overlay>
          </v-fade-transition>
          <v-card-text>
            <div>{{ app.description }}</div>
            <div v-if="app.code.link">
              <div class="title mt-2">Source Code</div>
              <v-btn icon :href="app.code.link" target="_blank">
                <v-icon>{{ app.code.icon }}</v-icon>
              </v-btn>
            </div>
          </v-card-text>
        </v-card>
      </v-card>
    </template>
  </v-hover>
</template>

<script>
export default {
  data() {
    return {
      overlay: false,
    };
  },
  props: {
    app: {
      type: Object,
      default: function () {
        return {};
      },
    },
  },
  methods: {
    logo(name) {
      return require("../assets/" + name);
    },
  },
};
</script>

